$border: 1px solid #E2E6E9;

$whiteColor: #fff;
$grayColor: #313237;
$liteGrayColor: #89939a;
$iconsColor: #b4bdc4;
$elementsColor: #e2e6e9;
$hoverColor: #fafbfc;
$greenColor: #27ae60;
$redColor: #eb5757;

$pagePadding: 0 152px;
$pageHeight: 70vh;


@mixin onPhone {
  @media (max-width:640px) {
    @content;
  }
}

@mixin onDesktop {
  @media (max-width:1024px) {
    @content;
  }
}



